.sidebar {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 250px;
    padding: 10px 14px;
    background: var(--sidebar-color);
    transition: var(--tran-05);
    z-index: 100;

    &.close {
        width: 100px;

        .text {
            opacity: 0;
        }

        .toggle {
            transform: translateY(60%) rotate(0deg);
        }

        &~.home {
            left: 78px;
            height: 100vh;
            width: calc(100% - 78px);
        }
    }

    li {
        height: 50px;
        list-style: none;
        display: flex;
        align-items: center;
        margin-top: 10px;

        &.search-box {
            border-radius: 6px;
            background-color: var(--primary-color-light);
            cursor: pointer;
            transition: var(--tran-05);

            input {
                height: 100%;
                width: 100%;
                outline: none;
                border: none;
                background-color: var(--primary-color-light);
                color: var(--text-color);
                border-radius: 6px;
                font-size: 17px;
                font-weight: 500;
                transition: var(--tran-05);
            }
        }

        a {
            list-style: none;
            height: 100%;
            background-color: transparent;
            display: flex;
            align-items: center;
            height: 100%;
            width: 100%;
            border-radius: 6px;
            text-decoration: none;
            transition: var(--tran-03);

            &:hover {
                background-color: var(--hover-color);
            }
        }
    }

    .icon {
        min-width: 60px;
        border-radius: 6px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 20px;
    }

    .text {
        font-size: 17px;
        font-weight: 500;
        white-space: nowrap;
        opacity: 1;
    }

    .nav-text {
        margin-left: 2em;
    }

    header {
        position: relative;

        .image-text {
            display: flex;
            align-items: center;
        }

        .logo-text {
            display: flex;
            flex-direction: column;
        }

        .image {
            display: flex;
            align-items: center;
            justify-content: center;

            img {
                width: 40px;
                border-radius: 6px;
            }
        }

        .toggle {
            position: absolute;
            top: 50%;
            right: -25px;
            transform: translateY(-50%) rotate(180deg);
            height: 25px;
            width: 25px;
            background-color: var(--primary-color);
            color: var(--sidebar-color);
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 22px;
            cursor: pointer;
            transition: var(--tran-05);
        }
    }

    .menu {
        margin-top: 40px;
    }

    .menu-bar {
        height: calc(100% - 55px);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        overflow-y: scroll;
    }
}

.sidebar header .image,
.sidebar .icon {
    min-width: 60px;
    border-radius: 6px;
}

.sidebar .text,
.sidebar .icon {
    color: var(--text-color);
    transition: var(--tran-03);
}

header {
    .image-text {
        .name {
            margin-top: 2px;
            font-size: 15px;
            font-weight: 600;
            position: relative;
            top: 2px;
            right: 5px;
        }

        .profession {
            font-size: 16px;
            margin-top: -2px;
            display: block;
        }
    }
}

body {
    &.dark {
        .sidebar {
            header {
                .toggle {
                    color: var(--text-color);
                }
            }
        }


        .home {
            .text {
                color: var(--text-color);
            }
        }
    }
}

.sidebar li a:hover .icon,
.sidebar li a:hover .text {
    color: var(--sidebar-color);
}

body.dark .sidebar li a:hover .icon,
body.dark .sidebar li a:hover .text {
    color: var(--text-color);
}

.menu-bar {
    &::-webkit-scrollbar {
        display: none;
    }

    .bottom-content {
        .toggle-switch {
            position: absolute;
            right: 0;
            height: 60%;
            min-width: 55px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 6px;
            cursor: pointer;
        }
    }
}


